<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4">
        <div v-for="(button, index) in buttons" :key="index" class="col-md-auto">
          <button class="btn btn-outline-primary border-1"
                  :class="{ 'btn-primary text-white': isCurrentRoute(button.route) }"
                  @click="selectPage(button.route)">
            {{ button.label }}
          </button>
        </div>
      </div>
      
      <component :is="currentPage" />
    </div>
  </section>
</template>

<script>
import civil from '@/modules/legal/datos/reportes/civil.vue';
import penal from '@/modules/legal/datos/reportes/penal.vue';
import arbitral from '@/modules/legal/datos/reportes/arbitral.vue';
import indecopi from '@/modules/legal/datos/reportes/indecopi.vue';

export default {
  name: 'Registro',
  components: {
    civil,
    penal,
    arbitral,
    indecopi
  },
  data() {
    return {
      currentPage: 'civil',
      buttons: [
        { label: 'Reporte Civil Laboral', route: 'civil' },
        { label: 'Reporte Penal', route: 'penal' },
        { label: 'Reporte Arbitral', route: 'arbitral' },
        { label: 'Reporte Indecopi', route: 'indecopi' }
      ]
    };
  },
  methods: {
    selectPage(route) {
      this.currentPage = route;
    },
    isCurrentRoute(route) {
      return this.currentPage === route;
    }
  }
};
</script>

<style scoped>
.btn-outline-primary {
  color: #007bff;
  border-color: #007bff;
}

.btn-primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}
</style>
